.ManageLocationsPage {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  &__container {
    transform: translate(-50%, -50%);
    max-width: 48rem;
    width: 100%;
    z-index: 20;
    position: absolute;
    top: 50%;
    left: 50%;
    display: flex;
    flex-direction: column;
    padding: 3rem;
  }

  &__title {
    margin-bottom: 1rem;
    font-size: 2rem;
  }

  &__list {
    list-style-type: none;
    text-transform: uppercase;
    font-size: 1.2rem;
  }

  &__li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(#585858, 0.1);
    padding: 0.5rem 0;
  }

  &__buttons {
    margin-left: 5rem;
  }

  &__button {
    border: 1px solid #585858;
    border-radius: 5px;
    padding: 0.5rem;
    background: transparent;
    margin: 0.5rem;
    cursor: pointer;
    transition: all 0.2s ease;
    text-transform: uppercase;

    &:hover {
      background: rgba(white, 0.3);
      box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
    }
  }

  &__closeButton {
    border: 1px solid #585858;
    border-radius: 5px;
    width: 100%;
    padding: 0.5rem;
    background: transparent;
    margin: 2rem 00;
    cursor: pointer;
    transition: all 0.2s ease;
    text-transform: uppercase;

    &:hover {
      background: rgba(white, 0.3);
      box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
    }
  }
}
