.DepthScale {
  height: 90vh;
  position: absolute;
  right: 5rem;
  top: 50%;
  padding: 0rem;
  transform: translateY(-50%);
  border-right: 1px solid #ccc;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 1.2rem;

  @media (max-width: 50em) {
    right: 2rem;
  }

  &__midmarker {
    @media (max-width: 28em) {
      display: none;
    }
  }
}
