.Location {
  position: absolute;
  left: 50%;
  bottom: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translate(-50%, -50%);
  mix-blend-mode: darken;
  z-index: 20;
  color: #585858;

  // @media (max-width: 50em) {
  //   bottom: 4rem;
  // }

  &__label {
    margin-bottom: 0.5rem;
    font-size: 1.5rem;
    font-family: inherit;
  }

  &__input {
    width: 30rem;
    border: 1px solid #585858;
    padding: 0.5rem;
    outline: none;
    background-color: transparent;
    font-family: inherit;
    text-align: center;
    font-size: 1.5rem;
  }

  &__manageBtn {
    border: none;
    background: transparent;
    margin-top: 0.5rem;
    color: #585858;
    cursor: pointer;
  }

  &__warn {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
}
