.Waves {
  height: 100vh;
  width: 100%;
  position: absolute;
  overflow: hidden;
  bottom: 0;
  left: 0;

  &__wave1 {
    width: 100%;
    position: absolute;
    background-repeat: repeat-x;
    background-size: 1438px 100%;
    background-position: bottom;
    bottom: 0;
    height: 0;
    left: 0;
    animation: wave2 3s linear infinite both;
    z-index: 1;
    opacity: 0.6;
    transition: all 2s ease-in-out;
  }

  &__wave2 {
    width: 100%;
    position: absolute;
    bottom: 0;
    background-size: 1438px 100%;
    left: 0;
    background-repeat: repeat-x;
    animation: wave1 4s linear infinite both;
    mix-blend-mode: darken;
    opacity: 0.3;
    z-index: 10;
    transition: all 1.5s ease-in-out;
  }

  &__wave3 {
    width: 100%;
    position: absolute;
    bottom: 0;
    background-size: 3000px 100%;
    left: 0;
    background-repeat: repeat-x;
    animation: wave3 4s linear infinite both;
    opacity: 0.3;
    z-index: 10;
    transition: all 1.8s ease-in-out;
  }
}

@keyframes wave1 {
  0% {
    background-position-x: 0;
  }

  100% {
    background-position-x: 1438px;
  }
}

@keyframes wave2 {
  0% {
    background-position-x: 0;
  }

  100% {
    background-position-x: -1438px;
  }
}

@keyframes wave3 {
  0% {
    background-position-x: 0;
  }

  100% {
    background-position-x: -3000px;
  }
}
