.Status {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  text-transform: lowercase;
  display: grid;
  grid-template-columns: min-content min-content min-content;
  grid-auto-rows: min-content;
  align-items: baseline;

  &__text {
    font-size: 12.5rem;
    color: #585858;
    grid-row: 1;
    grid-column: 1;
    margin: 0;
    padding: 0;
    line-height: 0;
    margin-right: 1rem;

    @media (max-width: 31em) {
      font-size: 10.5rem;
    }
  }

  &__units {
    font-size: 5rem;
    grid-row: 1;
    grid-column: 2;
  }

  &__arrow {
    height: 100%;
    grid-row: 1 / 2;
    grid-column: 3;
    margin-left: 2rem;
    align-self: end;
    margin-bottom: 1.5rem;
  }

  &__small {
    font-size: 1.3rem;
    margin-left: 1rem;
    grid-row: 2;
    grid-column: 1/3;
    text-align: right;
  }
}
